
import { defineComponent, onMounted, reactive } from "vue";
import Button from "@/components/common/Button.vue";
import Badge from "@/components/common/Badge.vue";
import { BtnStyle } from "@/types/enums/BtnStyle";
import { Size } from "@/types/enums/Size";
import { NavbarStyle } from "@/types/enums/NavbarStyle";

export default defineComponent({
  name: "NotFound",

  emits: ["navbar-style"],

  components: {
    Button,
    Badge,
  },

  setup(props, { emit }) {
    onMounted(() => {
      emit("navbar-style", NavbarStyle.Default);
    });

    /*  CTA: Naar startpagina */
    const ctaBtn = reactive({
      style: BtnStyle.Dark,
      size: Size.Big,
    });

    return {
      ctaBtn,
    };
  },
});
