class NavLink {
  public name = "";
  public link = "";
}

class GroupNavLink extends NavLink {
  public isOpen = false;
  public children: NavLink[] = [];
}

class NavLinkArray
{
  public links: NavLink[] = [];
  public closeAll() : void {
    this.links.forEach(link => {
      const groupLink = link as GroupNavLink;
      if(groupLink) {
        groupLink.isOpen = false;
      }
    })
  }
}

const navLinks: Array<NavLink> = [
  {
    name: "Diensten",
    link: "/diensten",
    isOpen: false,
    children: [
      {
        name: "Webapp ontwikkeling",
        link: "/diensten/webapp-ontwikkeling",
      },
      {
        name: "Api ontwikkeling",
        link: "/diensten/api-ontwikkeling",
      },
    ],
  } as GroupNavLink,
  {
    name: "Hosting",
    link: "/hosting",
    isOpen: false,
    children: [
      {
        name: "Web Hosting",
        link: "/hosting/web-hosting",
      },
      {
        name: "API hosting",
        link: "/hosting/api-hosting",
      },
    ],
  } as GroupNavLink,
  {
    name: "Over ons",
    link: "/over-ons",
  } as NavLink,
  {
    name: "Contact",
    link: "/contact",
  } as NavLink,
];

const navLinksInstance = new NavLinkArray();
navLinksInstance.links = navLinks;

export { NavLink, GroupNavLink, NavLinkArray, navLinksInstance }

