
import { defineComponent, reactive, watchEffect } from "vue";
import { HamburgerStyle } from "@/types/enums/HamburgerStyle";
import EnumHelper from "@/helpers/EnumHelper";
const EnumProp = EnumHelper.EnumProp;

export default defineComponent({
  name: "TheHamburger",

  props: {
    hamburgerStyle: EnumProp(HamburgerStyle.Default, HamburgerStyle),
    active: {
      type: Boolean,
      required: true,
    },
  },

  setup(props) {
    const hamburgerClassBase = "hamburger hamburger--collapse main-color";

    const hamburger = reactive({
      active: props.active,
      class: hamburgerClassBase,
      classAddition: "",
    });

    watchEffect(() => {
      hamburger.classAddition =
        props.hamburgerStyle == HamburgerStyle.Inverse ? " inverse" : "";
    });

    watchEffect(() => {
      hamburger.active = !hamburger.active;
      hamburger.class = props.active
        ? hamburgerClassBase + " is-active"
        : hamburgerClassBase;
    });

    return { hamburger };
  },
});
