<template>
  <div class="banner" :style="style">
    <div class="banner-container">
      <div class="banner-container-content">
        <h1 class="title" id="main-title">{{ title }}</h1>
        <p>
          {{ text }}
        </p>
        <slot></slot>
      </div>
    </div>
  </div>
</template>


<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
  name: "Banner",

  props: {
    title: {
      type: String,
      required: true,
    },
    text: {
      type: String,
      required: true,
    },
    imgSource: {
      type: String,
      required: true,
    },
  },

  setup(props) {
    let style = `background-image: url('${require(`@/assets/` +
      props.imgSource)}')`;
    return { style };
  },
});
</script>

