
import { defineComponent, reactive, watchEffect } from "vue";
import Button from "@/components/common/Button.vue";
import TheHamburger from "@/components/single/TheHamburger.vue";
import { BtnStyle } from "@/types/enums/BtnStyle";
import { Size } from "@/types/enums/Size";
import { GroupNavLink, NavLink, navLinksInstance } from "@/types/NavLink";
import { NavbarStyle } from "@/types/enums/NavbarStyle";
import EnumHelper from "@/helpers/EnumHelper";
import { HamburgerStyle } from "@/types/enums/HamburgerStyle";
const EnumProp = EnumHelper.EnumProp;

export default defineComponent({
  name: "TheNavigationbar",

  props: {
    navbarStyle: EnumProp(NavbarStyle.Default, BtnStyle),
  },

  components: {
    Button,
    TheHamburger,
  },

  setup(props) {
    /*  CTA: Project bespreken */
    const ctaBtn = reactive({
      style: BtnStyle.Dark,
      size: Size.Medium,
    });

    var dropdown = reactive({
      list: new Array<NavLink>(),
      isOpen: false,
      open: (group: GroupNavLink) => {
        if (dropdown.list == group.children) {
          // Clicked second time, close.
          navbar.linkModel.closeAll();
          dropdown.close();
          return;
        } else if (dropdown.isOpen) {
          // Other GroupNav clicked, clear list.
          dropdown.list = Array<NavLink>();

          // Close icons for all, and set single.
          navbar.linkModel.closeAll();
          group.isOpen = true;
        } else {
          // Set hovered styling.
          dropdown.isOpen = true;
          group.isOpen = true;

          if (props.navbarStyle != NavbarStyle.Default) {
            useNavbarStyle(NavbarStyle.Default);
          }
        }
        dropdown.list = group.children;
      },
      close: () => {
        dropdown.isOpen = false;
        dropdown.list = Array<NavLink>();
        navbar.linkModel.closeAll();
      },
      x: 0,
      passClickEvent: (e: MouseEvent) => {
        let element: HTMLElement = e.target as HTMLElement;
        if (element.className.toString().includes("fas")) {
          // Got inner i tag, select parent div instead.
          element = element.parentElement as HTMLElement;
        }
        const rect = (element as Element).getBoundingClientRect();
        dropdown.x = rect.x;
      },
      style: () => {
        return {
          left: dropdown.x + "px",
        };
      },
    });

    /* Nav */
    const navbar = reactive({
      logoUrl: require("../../assets/logo.svg"),
      class: "",
      linkModel: navLinksInstance,
      mobileBarOut: false,
      hamburgerActive: false,
      hamburgerStyle: HamburgerStyle.Default,

      hoverLeave() {
        if (dropdown.isOpen) dropdown.close();
      },

      hamburgerClick: () => {
        // Toggle hamburger classes.
        navbar.hamburgerActive = !navbar.hamburgerActive;

        // Set mobile menu classes..
        navbar.mobileBarOut = !navbar.mobileBarOut;

        // When mobile menu is out navbar style must be default.
        if (navbar.mobileBarOut) {
          if (props.navbarStyle != NavbarStyle.Default) {
            useNavbarStyle(NavbarStyle.Default);
          }
        } else {
          navbar.linkModel.closeAll();
        }
      },

      logoClick: () => {
        if (navbar.mobileBarOut) {
          // Mobile bar is out, tuck back in. Else ignore.
          navbar.hamburgerClick();
        }
      },
    });

    const useNavbarStyle = (style: NavbarStyle) => {
      navbar.class = "";
      switch (style) {
        case NavbarStyle.Transparant:
          navbar.class += " transparant";
          navbar.logoUrl = require("../../assets/logo.svg");
          navbar.hamburgerStyle = HamburgerStyle.Default;
          ctaBtn.style = BtnStyle.Dark;
          break;
        case NavbarStyle.TransparantInverse:
          navbar.class += " transparant-inverse";
          navbar.logoUrl = require("../../assets/logo-inverse.svg");
          navbar.hamburgerStyle = HamburgerStyle.Inverse;
          ctaBtn.style = BtnStyle.DarkBordered;
          break;
        default:
          navbar.logoUrl = require("../../assets/logo.svg");
          navbar.hamburgerStyle = HamburgerStyle.Default;
          ctaBtn.style = BtnStyle.Dark;
      }
    };

    watchEffect(() => {
      // Mobile bar should always have Navbar.Default sty ling when out regardless of the prop.
      if (!navbar.mobileBarOut && !dropdown.isOpen) {
        useNavbarStyle(props.navbarStyle);
      }
    });

    return {
      navbar,
      ctaBtn,
      dropdown,
    };
  },
});
