import { NavbarStyle } from '@/types/enums/NavbarStyle'
import { onMounted, onUnmounted } from 'vue';

export type LooseRequired<T> = { [P in string & keyof T]: T[P] }

export default {
  setup(props: Readonly<LooseRequired<unknown>>, context: { emit: (event: "navbar-style", ...args: NavbarStyle[]) => void; }, initStyle: NavbarStyle, mainStyle: NavbarStyle): void {

    let latestEmit = -1;
    let scroll = window.top.scrollY;

    const handleScroll = () => {
      scroll = window.top.scrollY;

      if (scroll > 0) {
        // Scroll should be main
        if (latestEmit != mainStyle) {
          latestEmit = mainStyle;
          context.emit("navbar-style", mainStyle);
        }

      } else {
        if (latestEmit != initStyle) {
          latestEmit = initStyle;
          context.emit("navbar-style", initStyle);
        }
      }
    };

    onMounted(() => {
      handleScroll();
      window.addEventListener("scroll", handleScroll);
    });

    onUnmounted(() => {
      window.removeEventListener("scroll", handleScroll);
    });
  }
}