// eslint-disable-next-line  @typescript-eslint/no-explicit-any
function EnumProp(d: string | number, e: Record<any, any>) : any {
  // <- Record is changed
  return {
    default: d,
    /* eslint-disable-next-line  @typescript-eslint/no-explicit-any */
    validator: (v: string | number) => e[v] !== undefined,
  };
}

const EnumHelper = { EnumProp };
export default EnumHelper;