import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'
import Home from '@/views/Home.vue'
import NotFound from "@/views/NotFound.vue";
import AboutUs from "@/views/AboutUs.vue";
import Contact from "@/views/Contact.vue";
import Webhosting from "@/views/hosting/Webhosting.vue";
import ApiHosting from "@/views/hosting/ApiHosting.vue";
import ApiDevelopment from "@/views/development/ApiDevelopment.vue";
import WebappDevelopment from "@/views/development/WebappDevelopment.vue";


const routes: Array<RouteRecordRaw> = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/over-ons',
    name: 'AboutUs',
    component: AboutUs
  },
  {
    path: '/contact',
    name: 'Contact',
    component: Contact
  },
  {
    path: '/diensten/webapp-ontwikkeling',
    name: 'Web ontwikkeling',
    component: WebappDevelopment
  },
  {
    path: '/diensten/api-ontwikkeling',
    name: 'API ontwikkeling',
    component: ApiDevelopment
  },
  {
    path: '/hosting/web-hosting',
    name: 'Webhosting',
    component: Webhosting
  },
  {
    path: '/hosting/api-hosting',
    name: 'API Hosting',
    component: ApiHosting
  },
  {
    path: '/not-found',
    name: 'Not found',
    //   // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    //   // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '@/views/NotFound.vue')
  },
  { path: '/:pathMatch(.*)', name: 'bad-not-found', component: NotFound },
]


const router = createRouter({
  //history: createWebHistory(process.env.BASE_URL),
  history: createWebHistory(""),
  routes,
})

export default router
