
import { defineComponent, onMounted, reactive } from "vue";
import TheFooter from "@/components/single/TheFooter.vue";
import SmallBanner from "@/components/common/SmallBanner.vue";
import Button from "@/components/common/Button.vue";
import Post from "@/components/common/Post.vue";

import { NavbarStyle } from "@/types/enums/NavbarStyle";
import { BtnStyle } from "@/types/enums/BtnStyle";
import { Size } from "@/types/enums/Size";

export default defineComponent({
  name: "ApiHosting",

  emits: ["navbar-style"],

  components: {
    TheFooter,
    SmallBanner,
    Button,
    Post,
  },

  setup(props, { emit }) {
    onMounted(() => {
      emit("navbar-style", NavbarStyle.Default);
    });

    /*  CTA: Naar ? */
    const ctaBtn = reactive({
      style: BtnStyle.Dark,
      size: Size.Big,
    });

    return {
      ctaBtn,
    };
  },
});
