<template>
  <button :class="btnClass">
    {{ text }}
  </button>
</template>

<script lang="ts">
import { defineComponent, ref, watchEffect } from "vue";
import { BtnStyle } from "@/types/enums/BtnStyle";
import { Size } from "@/types/enums/Size";
import EnumHelper from "@/helpers/EnumHelper";
const EnumProp = EnumHelper.EnumProp;

export default defineComponent({
  name: "Button",

  props: {
    text: {
      type: String,
      required: true,
    },
    style: EnumProp(BtnStyle.Dark, BtnStyle),
    size: EnumProp(Size.Medium, Size),
  },

  setup(props) {
    // Init.
    const btnClass = ref("");

    watchEffect(() => {
      btnClass.value = "btn";

      switch (props.size) {
        case Size.Big:
          btnClass.value += " big";
          break;
        case Size.Medium:
          btnClass.value += " medium";
          break;
        case Size.Small:
          btnClass.value += " small";
          break;
        default:
          btnClass.value += " medium";
          break;
      }

      switch (props.style) {
        case BtnStyle.Dark:
          btnClass.value += " dark";
          break;
        case BtnStyle.Light:
          btnClass.value += " light";
          break;
        case BtnStyle.LightBordered:
          btnClass.value += " light bordered";
          break;
        case BtnStyle.DarkBordered:
          btnClass.value += " dark bordered";
          break;
      }
    });

    return { btnClass };
  },
});
</script>

<style scoped lang="scss">
@import "@/scss/main.scss";

.btn {
  font-family: 'Kumbh Sans', sans-serif;
  letter-spacing: 1px;
  text-transform: uppercase;
  border-radius: 5px;
  box-shadow: $shadow;
  cursor: pointer;

  &.big {
    padding: 1em 1.4em;
    font-size: 18px;
  }

  &.medium {
    padding: 1em 2.8em;
    font-size: 14px;
  }

  &.small {
    padding: 0.5em 2.3em;
    font-size: 10px;
  }
}

.dark {
  background: $main-bg-color;
  color: $white-color;
  border: 0px solid $white-color;
}

.light {
  background: $white-color;
  color: $main-color;
  border: 0px solid $main-color;
}

.bordered {
  background: transparent;
  border-width: 3px;
}

@media only screen and (max-width: 600px) {
  .btn {

    &.big {
      font-size: 14px;
    }

    &.medium {
      font-size: 12px;
    }

    &.small {
      font-size: 8px;
    }
  }
}
</style>