    /*  CTA: Naar ? */
    const ctaBtn = reactive({
      style: BtnStyle.Dark,
      size: Size.Medium,
    });

    /*  CTA: Naar ? */
    const ctaKnowMoreBtn = reactive({
      style: BtnStyle.Dark,
      size: Size.Medium,
