
import { defineComponent, reactive } from "vue";
import TheFooter from "@/components/single/TheFooter.vue";
import Button from "@/components/common/Button.vue";
import Card from "@/components/common/Card.vue";
import Banner from "@/components/common/Banner.vue";

import { BtnStyle } from "@/types/enums/BtnStyle";
import { Size } from "@/types/enums/Size";
import ScrollMixin from "@/mixins/Scroll";
import { NavbarStyle } from "@/types/enums/NavbarStyle";

export default defineComponent({
  name: "AboutUs",

  emits: ["navbar-style"],

  components: {
    TheFooter,
    Button,
    Card,
    Banner,
  },

  setup(props, { emit }) {
    ScrollMixin.setup(
      props,
      { emit },
      NavbarStyle.TransparantInverse,
      NavbarStyle.Default
    );

    /*  CTA: Naar ? */
    const ctaBtn = reactive({
      style: BtnStyle.Dark,
      size: Size.Medium,
    });
    return {
      ctaBtn,
    };
  },
});
