
import { defineComponent } from "vue";

export default defineComponent({
  name: "Banner",

  props: {
    title: {
      type: String,
      required: true,
    },
    text: {
      type: String,
      required: true,
    },
    imgSource: {
      type: String,
      required: true,
    },
  },

  setup(props) {
    let style = `background-image: url('${require(`@/assets/` +
      props.imgSource)}')`;
    return { style };
  },
});
