
import TheNavigationbar from "@/components/single/TheNavigationbar.vue";
import { defineComponent, reactive } from "vue";
import { NavbarStyle } from "./types/enums/NavbarStyle";

export default defineComponent({
  name: "App",

  components: {
    TheNavigationbar,
  },

  setup() {
    const navbar = reactive({
      style: NavbarStyle.TransparantInverse,
      onSetStyle: (style: NavbarStyle) => {
        navbar.style = style;
      },
    });
    return { navbar };
  },
});
